import {APIRequest} from '../framework/APIrequest'

process.env.API_URL = 'https://my-json-server.typicode.com/Xotabu4/demo'


import * as RAML from 'raml-1-parser'
import { URL } from 'url'
import * as URItemplate from 'uri-templates'

let RAML_API: RAML.api10.Api = null;


function parseAPI(ramlFilePath: string = './../api.raml'): RAML.api10.Api {
    if (!RAML_API) {
        RAML_API = RAML.loadApiSync(ramlFilePath) as RAML.api10.Api
    }
    return RAML_API
}
parseAPI()
